*{
    padding: 0;
    margin: 0;
}

body{
    height: 100vh;
}
header{
    background-color: #30153C;
}

.title{
    text-align: center;
    color: white;
    padding: 50px;
}


.form{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-top: 95px;
}

.form-content{
    display: flex;
    flex-direction: column;
    align-content: center;
}

.form-content label{
    color: white;
    font-weight: bolder;
    display: flex;
    justify-content: center;
}
.form-content p{
    color: white;
    text-align: center;
    border-radius: 30px;
    padding: 5px;
    margin: 2px;
}

.form-content select{
    width: 150px;
    height: 45px;
    border-radius: 20px;
    padding: 10px;
    background-color: #D781FC;
    color: white;
    font-weight: bold;
    font-size: 1.3rem;
}

.create{
    display: flex;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;
}

.create button{
    background-color: #D781FC;
    border-color: #D781FC;
    border-radius: 20px;
    padding: 10px;
    color: white;
    font-weight: bold;
    width: 200px;
    height: 50px;
    font-size: 0.5rem;
}

footer{
    background-color: #30153C;
    color: white;
    font-weight: bolder;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 30vh;
    padding-top: 55px;
    padding-bottom: 55px;
}

.equation{
    /* border: 1px solid darkblue; */
    display: flex;
    align-items: center;
}

.termino{
    /* border: 1px solid red; */
    display: flex;
    flex-direction: row;
    padding: 3px;
}

.notacion{
}

.variable{
    /* border: 1px solid darkturquoise; */
    display: flex;
    align-content: flex-end;
}
.exponente{
    font-size: 1.5rem;
}

.funContainer{
    display: inline-flex;
    align-items: center;
    vertical-align: middle;
    font-size: 3rem;
    width: auto;
    
}
.funContainer div{
    margin: 0 1.5px 0 1.5px;
}


.operation{
    display: flex;
}
.conjunto{
    display: block;
}

img{
    width: 100vw;
    height: 100vh;
}

.orden{
    background-color: red;
}