.integralsPage{
    width: 100vw;
    height: 100vh;
    position: fixed;
    background-color: #181818;
}

.integralsHeader{
    position: fixed;
    top: 0;
    width: 100vw;
    height: 15vh;
    color: white;
}

.integralsHeader a{
    display: inline-block;
    width: 25vw;
    text-align: center;
    font-size: 1.2rem;
    line-height: 15vh; 
    text-decoration: none;  
    color: white;
}
.integralsHeader p{
    display: inline-block;
    width: 25vw;
    text-align: center;
    font-size: 1.2rem;
    line-height: 15vh;
}
.integralsHeader strong{
    display: inline-block;
    width: 50vw;
    text-align: center;
    font-size: 2rem;
    line-height: 15vh;
}


.inputDiv{
    position: fixed;
    top: 20vh;
}
.inputDiv div{
    display: inline-block;
}

.outputDiv{
    position: fixed;
    bottom: 5vh;
}
.outputDiv strong{
    display: inline-block;
    width: 100vw;
    line-height: 6vh;
    color: white;
    font-size: 1.3rem;
    text-align: center;
}

 /* INPUT OF INTEGRALS */
 .rules{
    position: relative;
    width: 30vw;
    height: 40vh;
    margin-left: 2vw;
    border-radius: 15px;
 }
 .rules p{
     color: white;
     font-size: 1.2rem;
     line-height: 6vh;
 }
.calculator{
    position: relative;
    width: 30vw;
    height: 40vh;
    margin-left: 3vw;
    border-radius: 15px;
}
.calculator input, .calculator button{
    padding: 10px 20px;
    border: none;
    border-radius: 30px;
    margin-top: 5vh;
    text-align: center;
}
.inputDiv strong{
    color: white;
    font-size: 1.5em;
    line-height: 10vh;
}
.calculator p, .results p{
    color: white;
    font-size: 1.3r em;
    line-height: 10vh;
    font-weight: bold;
}

.results{
    position: relative;
    width: 30vw;
    height: 40vh;
    margin-left: 3vw;
    border-radius: 15px;
}



/* EXAMPLES DIV */
.examples{
    position: relative;
    height: auto;
}
.examples div{
    display: inline-flex;
    margin: 10px;
    border-radius: 10px;
    filter: opacity(.6);
    transition: 200ms;
}
.examples div:hover{
    filter: opacity(1.2);
    transform: scale(1.1);
}